body {
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

ul {
  padding: 0;
}
  
li {
  list-style-type: none;
}

.avatar {
  width: 150px; 
  border-radius: 50%;
}

.space-list-items {
  margin-bottom: 7px; 
}

.languages {
  display: flex;
  justify-content: center;
}

.languages li {
    margin: 10px;
    font-weight: bold;
    cursor: pointer;
}

.popular-list {
  display: flex;
  flex-wrap: wrap; 
  justify-content: space-around; 
}

.popular-item {
  margin: 20px; 
  text-align: center;
}

.popular-rank {
  font-size: 20px;
  margin: 10px;
}